import { Card, Col } from 'antd';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import cheerio from 'cheerio';

const CardContainer = ({ span, children, url, id, delay }) => {
    const [title, setTitle] = useState(null);
    const [imgSrc, setImgSrc] = useState(null);
    useEffect(() => {
        if(url) {
            axios.get(url).then(res => {
                const _ = cheerio.load(res.data);
                // console.log(res.data);
                _('head>title').each((index, item) => {
                    setTitle(item.children[0].data);
                })
                _('meta').each((index,item) => {
                    if(item.attribs.property === 'og:image') {
                        setImgSrc(item.attribs.content)
                    }
                })
                // _('.post-image').each((index, item) => {
                //     setImg(item.attribs['data-srcset']);
                //     setImgSrc(item.attribs.src);
                // })
            })
        }
    },[])
    return (
        <>
            <Col span={span} className="blog-col-card-container trigger" id={id} trigger="fadeIn" trigger-delay={delay}>
                <a href={url}>
                <Card className="blog-card" style={{backgroundImage: `url(${imgSrc})`}}>
                    {title}
                    {children}
                </Card>
                </a>
            </Col>
        </>
    )
}

export default CardContainer;